.card {
    background-color: #fff;
    text-align: center;
    height: 300px;
    width: 200px;
    border-radius: 8px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    overflow: hidden;
}

.product {
    height: 87%;
    background-color: #fff;
    padding: 6px;
}

.buy {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 13%;
    background-color: #03A9F4;
}

.thumbnail {
    height: 70%;
}

.title {
    height: 30%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 3vh;
    overflow: hidden;
}

.price {
    color: #fff;
    width: 80%;
}

